body {
  position: relative;
}

#header {
  height: 100vh;
  text-align: center;
  vertical-align: middle;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #323a45 url(../img/code.jpg) no-repeat fixed center/contain;

  .jumbotron {
    width: auto;
    display: inline-block;
    position: relative;
    border-radius: 20px;
  }
}

.freeze {
  overflow: hidden;

  h2 {
    display: inline-block;
    position: relative;
    top: 4rem;
  }

  .sidebox {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 8rem 2rem 2rem;
    background: url(../img/mountain.jpg) no-repeat center;

    .card-columns {
      column-count: auto;
      column-width: 300px;

      .card {
        -webkit-column-break-inside: avoid;
                  page-break-inside: avoid;
                       break-inside: avoid;
      }
    }

    &>div {
      position: absolute;
      overflow-y: scroll;
      margin: 9rem 2rem 2rem;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;

      &::-webkit-scrollbar {
        width: 0;
      }
    }
  }

  .content {
    position: absolute;
    overflow-y: scroll;
    top: 7rem;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 2rem;

    &::-webkit-scrollbar {
      width: 0;
    }
  }
}

.skrollr-body > div {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hover {
  transition: all 0.5s 0s;

  &:hover {
    font-size: 110%;
    color: white;

    small {
      color: rgba(0, 0, 0, 0.5) !important;
    }

    h5 {
      color: white;
    }
  }

  &-warning:hover { background-color: #f0ad4e; }
  &-info:hover { background-color: #5bc0de; }
  &-danger:hover { background-color: #d9534f; }
  &-success:hover { background-color: #5cb85c; }
  &-primary:hover { background-color: rgb(2, 117, 215); }

  &-inverse:hover {
    background-color: #373a3c;

    small {
      color: rgba(255, 255, 255, 0.5) !important;
    }
  }

  &-primary.hover-others.hover-group { background-color: rgba(2, 117, 215, 0.5); }
  &-warning.hover-others.hover-group { background-color: rgba(240, 173, 78, 0.5); }
  &-info.hover-others.hover-group { background-color: rgba(91, 192, 222, 0.5); }
  &-danger.hover-others.hover-group { background-color: rgba(217, 83, 79, 0.5); }
  &-success.hover-others.hover-group { background-color: rgba(92, 184, 92, 0.5); }
  &-inverse.hover-others.hover-group { background-color: rgba(0, 0, 0, 0.5); }
}

#about-yes, #about-no {
  display: none;
}

#footer {
  background: #222;
  color: white;

  .sidebox, .container {
    height: 100%;
  }

  .to-top {
    display: inline-block;
    position: relative;
    top: 8rem;
    transition: all 0.5s;

    &:hover {
      transform: rotate(360deg) scale(1.5)
    }

    a {
      color: inherit;

      &:hover, &:visited, &:link, &:active {
        text-decoration: none;
      }
    }

    p {
      margin: 0;
    }
  }

  .bottom {
    color: #555;
    position: absolute;
    bottom: 0;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;

    a {
      color: rgba(255, 255, 255, 0.5);
    }
  }
}

#konami {
  display: inline-block;
  opacity: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transition: all 2s;

  h4 {
    display: inline-block;
    transition: all 2s;
    &:hover {
      transform: rotate(720deg) scale(2);
    }
  }
}
